<template>
    <!-- <div class="uk-background-cover uk-flex uk-flex-center uk-flex-middle" style="background: url(assets/images/background/rogues.png) repeat-x top black"> -->

        <div class="uk-animation-slide-bottom-medium">  
            <div class="uk-width-2-3 uk-align-center uk-align-center uk-margin-remove-top">  

                <!-- LOGO -->
                <div v-if="data.logoSrc">
                    <div class="uk-flex uk-flex-center">
                        <div class="uk-width-1-3 uk-padding-small">
                            <img :src="data.logoSrc">
                        </div>
                    </div>
                </div>

                <!-- CONTENT BROWSER -->    
                <div :class="[ data.dark ? 'uk-background-secondary' : 'uk-background-muted' ]">
                    <div class="uk-grid uk-grid-small" uk-grid>
                        <div class="uk-width-expand">
                            <div class="uk-inline uk-width-1-1">

                                <!-- SINGLE IMAGE -->
                                <div v-if="data.content.length == 1">
                                    <div class="uk-flex uk-flex-center">
                                        <img :src="data.content[0].src">
                                    </div>
                                </div>

                                <!-- MULTI IMAGE -->
                                <div v-else>
                                    <div uk-slideshow="animation: push">
                                        <ul class="uk-slideshow-items">
                                            <li 
                                                v-for="(content, index) in data.content"
                                                :key="index">
                                                <img 
                                                    v-if="content.type == 'image'" 
                                                    :src="content.src"
                                                    uk-cover>
                                                <video 
                                                    v-if="content.type == 'video'" 
                                                    :src="content.src" 
                                                    uk-video 
                                                    uk-cover 
                                                    controls>
                                                </video>
                                                <iframe 
                                                    v-if="content.type == 'iframe' || content.type == 'youtube'" 
                                                    :src="content.src" 
                                                    style="width:100%; height:100%;" 
                                                    scrolling="no">
                                                </iframe>
                                            </li>
                                        </ul>
                                    </div> 

                                    <!-- PREVIEWS -->
                                    <div class="uk-margin-small-top uk-overflow-auto" style="overflow-y: hidden">
                                        <table class="uk-table uk-table-expand">
                                            <tr>
                                                <td v-for="(preview, index) in data.content" :key="index" class="uk-padding-remove">
                                                    <a :uk-slideshow-item="index">
                                                        <div class="uk-inline uk-dark">
                                                            <img :src="preview.preview" class="uk-preserve-width" width="140">
                                                            <div v-if="preview.type == 'youtube'" class="uk-overlay uk-position-center">
                                                                <a class="uk-icon-button" uk-icon="youtube"></a>
                                                            </div>
                                                        </div>
                                                    </a>
                                                </td>
                                                <td class="uk-width-expand uk-padding-remove"></td>
                                            </tr>
                                        </table>
                                    </div>
                                </div>
                            </div>
                        </div>

                        <!-- SIDEBAR INFO -->
                        <div class="uk-width-1-4@l uk-width-1-1@m">
                            <div class="uk-cover-container uk-height-small">
                                <img :src="data.imgSrc" uk-cover>
                            </div>
                            <div class="uk-margin uk-text-small" uk-overflow-auto>
                                <div class="uk-grid-collapse uk-child-width-1-1" uk-grid>
                                    <div v-if="data.title" class="uk-grid-small" uk-grid>
                                        <span uk-icon="info"></span>
                                        <span class="uk-text-bold">
                                            {{ data.title }}
                                        </span>
                                    </div>
                                    <div v-if="data.engine" class="uk-grid-small" uk-grid>
                                        <span uk-icon="database"></span>
                                        <span>{{ data.engine }}</span>
                                    </div>
                                    <div v-if="data.roles" class="uk-grid-small" uk-grid>
                                        <span uk-icon="users"></span>
                                        <span>{{ data.roles }}</span>
                                    </div>
                                    <div v-if="data.category" class="uk-grid-small" uk-grid>
                                        <span uk-icon="folder"></span>
                                        <span>{{ data.category }}</span>
                                    </div>
                                    <div v-if="data.duration" class="uk-grid-small" uk-grid>
                                        <span uk-icon="clock"></span>
                                        <span>{{ data.duration }}</span>
                                    </div>
                                    <div v-if="data.year" class="uk-grid-small" uk-grid>
                                        <span uk-icon="calendar"></span>
                                        <span>{{ data.year }}</span>
                                    </div>
                                </div>
                                <hr>
                                <div v-if="data.shortDescription" class="uk-margin uk-margin-right">
                                    <span class="uk-text-small uk-text-justify">
                                        {{ data.shortDescription }}
                                    </span>
                                </div>
                                <hr>
                                <div 
                                    v-if="data.tags" 
                                    class="uk-margin uk-margin-right uk-margin-bottom">
                                    <div class="uk-grid-collapse uk-width-1-1" uk-grid>
                                        <div 
                                            v-for="(tag, index) in data.tags" 
                                            :key="index" 
                                            class="uk-margin-remove">
                                            <span class="uk-label">
                                                {{ tag }}
                                            </span>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <hr>

                <!-- ACHIEVEMENTS -->    
                <div v-if="data.achievements.awards" class="uk-margin">
                    <div class="uk-card uk-card-small uk-card-default uk-card-body" :class="[ data.dark ? 'uk-card-secondary' : 'uk-card-default' ]">
                        <div class="uk-flex uk-flex-center">
                            <div class="uk-grid-medium uk-grid-divider" uk-grid>
                                <div v-if="data.achievements.awards" class="uk-width-auto">
                                    <div class="uk-grid-collapse" uk-grid>
                                        <div v-for="(award, index) in data.achievements.awards" :key="index">
                                            <img src="assets/images/awards/leaf_L.png" width="20">
                                            <img :src="award.img" width="60" :uk-tooltip="award.text">
                                            <img src="assets/images/awards/leaf_R.png" width="20">
                                        </div>
                                    </div>
                                </div>

                                <div v-if="data.achievements.nominations" class="uk-width-expand">
                                    <div class="uk-grid-collapse" uk-grid>
                                        <div 
                                            v-for="(nomination, index) in data.achievements.nominations" 
                                            :key="index">
                                            <img :src="nomination.img" width="60" :uk-tooltip="nomination.text">
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>

                <!-- DESCRIPTION -->
                <div v-if="data.description" class="uk-margin">
                    <div class="uk-card uk-card-small uk-card-default uk-card-body" :class="[ data.dark ? 'uk-card-secondary' : 'uk-card-default' ]">
                        <img src="assets/head.png" class="uk-align-left" width="60">
                        <div class="uk-text-justify">
                            <span v-html="data.description"></span>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    <!-- </div> -->
</template>

<script>
import Data from '@/assets/data/portfolio.json'

export default {
    data(){
        return {
            data: Data.portfolioData[this.$route.params.id]
        }
    }
}
</script>

<style>

    /* body{
        background-color:black;
        background-image: "assets/images/background/rogues.png";
    } */

</style>